const allMenus = (state, getters) => {
    console.log(getters.treeMenus)
    // 添加根菜单
    return [{
        menu_name: '全部菜单',
        menu_id: 0,
        route_id: 0,
        permission_id: "0",
        permission_code: "",
        permission_remark: "根菜单",
        resource_type_id: 2,
        resource_type_code: "",
        resource_type_name: "根菜单",
        children: getters.treeMenus
    }];
};
//转换为树形结构
const parseToTree = function (origin, list) {
    return list.map((parent) => {
        let children = origin.filter((child) => child.parent_id === parent.permission_id);
        if (children.length) {
            return { ...parent, children: parseToTree(origin, children) }
        } else {
            return { ...parent }
        }
    });
}
const treeMenus = (state) => {
    let cate_1st = state.menus.filter((route) => route.parent_id == 0);
    //深拷贝，防止修改原始数组
    return parseToTree(state.menus, cate_1st);
}

export default {
    allMenus,
    treeMenus,
}