// 储存路由数据
const STORE_ROUTES = (state, listData) => {
    state.routes = listData;
};

//添加路由
const APPEND_ROUTE = (state, routeData) => {
    state.routes.push(routeData);
}

//编辑路由
const UPDATE_ROUTE = (state, routeData) => {
    let route = state.routes.find((route) => route.route_id === routeData.route_id);
    //Object.assign 该方法使用源对象的[[Get]]和目标对象的[[Set]]，所以它会调用相关 getter 和 setter。
    Object.assign(route, routeData);
};

//删除路由
const DELETE_ROUTE = (state, route_id) => {
    let index = state.routes.findIndex((route) => route.route_id === route_id);
    state.routes.splice(index, 1);
}

export default {
    STORE_ROUTES,
    APPEND_ROUTE,
    UPDATE_ROUTE,
    DELETE_ROUTE,
}