// 储存菜单数据
const STORE_MENUS = (state, listData) => {
    state.menus = listData;
};

//添加菜单
const APPEND_MENU = (state, menuData) => {
    state.menus.push(menuData);
}

//编辑菜单
const UPDATE_MENU = (state, menuData) => {
    let menu = state.menus.find((menu) => menu.menu_id === menuData.menu_id);
    //Object.assign 该方法使用源对象的[[Get]]和目标对象的[[Set]]，所以它会调用相关 getter 和 setter。
    Object.assign(menu, menuData);
};

//删除菜单
const DELETE_MENU = (state, menu_id) => {
    let index = state.menus.findIndex((menu) => menu.menu_id === menu_id);
    state.menus.splice(index, 1);
}

export default {
    STORE_MENUS,
    APPEND_MENU,
    UPDATE_MENU,
    DELETE_MENU,
}