import { Route } from '@/api/';

// 加载所有路由
const load_route_list = async ({ commit }, query) => {
    let { status, data } = await Route.loadAll(query);
    if (status) {
        // 储存到state
        commit('STORE_ROUTES', data);
    }
    return { status, data };
};

//添加路由
const create_route = async ({ commit }, routeData) => {
    let { status, msg, data } = await Route.insert(routeData);
    if (status) {
        // 储存到state
        commit('APPEND_ROUTE', { ...data, ...routeData });
    }
    return { status, msg, data };
};

//编辑路由
const edit_route = async ({ commit }, routeData) => {
    let { status, msg } = await Route.update(routeData.route_id, routeData);
    if (status) {
        // 储存到state
        commit('UPDATE_ROUTE', routeData);
    }
    return { status, msg };
};

//删除路由
const remove_route = async ({ commit }, routeData) => {
    let { route_id, permission_id } = routeData;
    let { status, msg } = await Route.remove(route_id, { permission_id });
    if (status) {
        // 储存到state
        commit('DELETE_ROUTE', route_id);
    }
    return { status, msg };
};

export default {
    load_route_list,
    create_route,
    edit_route,
    remove_route,
}
