// 储存操作按钮数据
const STORE_OPERATIONS = (state, listData) => {
    state.operations = listData;
};

//添加操作按钮
const APPEND_OPERATION = (state, operationData) => {
    state.operations.push(operationData);
}

//编辑操作按钮
const UPDATE_OPERATION = (state, operationData) => {
    let operation = state.operations.find((operation) => operation.operation_id === operationData.operation_id);
    //Object.assign 该方法使用源对象的[[Get]]和目标对象的[[Set]]，所以它会调用相关 getter 和 setter。
    Object.assign(operation, operationData);
};

//删除操作按钮
const DELETE_OPERATION = (state, operation_id) => {
    let index = state.operations.findIndex((operation) => operation.operation_id === operation_id);
    state.operations.splice(index, 1);
}

export default {
    STORE_OPERATIONS,
    APPEND_OPERATION,
    UPDATE_OPERATION,
    DELETE_OPERATION,
}