import { Operation } from '@/api/';

// 加载所有操作按钮
const load_operation_list = async ({ commit }, query) => {
    let { status, data } = await Operation.list(query);
    if (status) {
        // 储存到state
        commit('STORE_OPERATIONS', data);
    }
    return { status, data };
};

//添加操作按钮
const create_operation = async ({ commit }, operationData) => {
    let { status, msg, data } = await Operation.insert(operationData);
    if (status) {
        // 储存到state
        commit('APPEND_OPERATION', { ...data, ...operationData });
    }
    return { status, msg, data };
};

//编辑操作按钮
const edit_operation = async ({ commit }, operationData) => {
    let { status, msg } = await Operation.update(operationData.operation_id, operationData);
    if (status) {
        // 储存到state
        commit('UPDATE_OPERATION', operationData);
    }
    return { status, msg };
};

//删除操作按钮
const remove_operation = async ({ commit }, operationData) => {
    let { operation_id, permission_id } = operationData;
    let { status, msg } = await Operation.remove(operation_id, { permission_id });
    if (status) {
        // 储存到state
        commit('DELETE_OPERATION', operation_id);
    }
    return { status, msg };
};

export default {
    load_operation_list,
    create_operation,
    edit_operation,
    remove_operation,
}
